<template id = "app">
    <div id="map" ref="rootmap">
      <div id="popup" class="ol-popup">
      <a href="#" id="popup-closer" class="ol-popup-closer" @click = "closePopup()"></a>
      <div id="popup-content">{{10+12}}</div>
    </div>
      <!-- <Popup id = "popup" name ="popup" msg=""/>  -->
    </div>
    
</template>

<script>
// import Vue from 'vue';
import "ol/ol.css";
import { Map, View,Overlay } from "ol";
import TileLayer from "ol/layer/Tile";
import XYZ from 'ol/source/XYZ';
// import Popup from '@/components/Popup.vue'

// new Vue({
//   el:"#app",
//   data:{

//     template: '<div>sdfsfsdf</div>'
//   }
// })

// import Popup from '@/components/Popup.vue'
export default {
  data() {
    return {
      map: null
    };
  },
  mounted() {
    this.initMap()
  },
   methods: {
    initMap(){


       var container = document.getElementById('popup');
      // var content = document.getElementById('popup-content');
      var closer = document.getElementById('popup-closer');
       
      var overlay = new Overlay({
        element: container,
        autoPan: true,
        autoPanAnimation: {
          duration: 250
        }
      });
      closer.onclick = function() {
        overlay.setPosition(undefined);
        closer.blur();
        return false;
      };
     this.map =  new Map({
      target: "map",
      overlays: [overlay],
      layers: [
        new TileLayer({
           source: new XYZ({
                    url:"https://mt1.google.cn/maps/vt?lyrs=s%40721&hl=zh-CN&gl=CN&x={x}&y={y}&z={z}"
                })
        })
      ],
      view: new View({
        projection: "EPSG:4326",    
        center: [120.064839,30.2548857], 
        zoom: 12
      })
    
    
    
    });
    this.map.on('singleclick', function(evt) {
      console.info("singleClicked"+evt);
      var coordinate = evt.coordinate;
        // var hdms = toStringHDMS(toLonLat(coordinate));

        // content.innerHTML = '<p>You clicked here:</p><code>' + hdms +
        //     '</code>';
        overlay.setPosition(coordinate);
    })
    },
    closePopup(){
      alert("hehe");
    }
  },
};
</script>

<style scoped>
#map{height:100%;}
/*ol*/
.ol-attribution,.ol-zoom { display: none;}
.ol-popup {
        position: absolute;
        background-color: white;
        -webkit-filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
        filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
        padding: 15px;
        border-radius: 10px;
        border: 1px solid #cccccc;
        bottom: 12px;
        left: -50px;
        min-width: 280px;
      }
      .ol-popup:after, .ol-popup:before {
        top: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
      }
      .ol-popup:after {
        border-top-color: white;
        border-width: 10px;
        left: 48px;
        margin-left: -10px;
      }
      .ol-popup:before {
        border-top-color: #cccccc;
        border-width: 11px;
        left: 48px;
        margin-left: -11px;
      }
      .ol-popup-closer {
        text-decoration: none;
        position: absolute;
        top: 2px;
        right: 8px;
      }
      .ol-popup-closer:after {
        content: "";
      }
</style>